@import '_includes';

.spinner-button {
  &.md-button[disabled] {
    color: $color-white;
    background-color: $color-red-base;
  }

  & md-progress-circular {
    margin-left: auto;
    margin-right: auto;

    & path {
      stroke: $color-white;
    }
  }
}




