@import '_includes';

#list-table-container {
	border-radius: 4px;
	margin: rem(60px) rem(60px) rem(20px) rem(60px);
	.md-table {
		width: 100%;
		border-spacing: 0;
		overflow: hidden;
		background-color: white;
		border: 1px solid #e1e1e1;
		border-bottom: 0;
		border-top-left-radius: 4px;
		border-top-right-radius: 4px;
		.md-thead {
			text-align: left;
			th {
				height: 56px;
				vertical-align: middle;
				border-bottom: 3px solid #e1e1e1;
				padding-left: rem(25px);
				box-sizing: border-box;
				color: $color-gray-light;
				outline: 0;
				cursor: pointer;
				user-select: none;

				&:last-child {
					position: relative;

					& .input-expandable {
						top: 50%;
						transform: translateY(-50%);
					}
				}
			}
		}
		.md-row {
			height: 56px;
			td {
				padding-left: rem(25px);
				color: $color-gray-light;
				border-bottom: 2px solid #e1e1e1;

				button {
					width: rem(40px);
					height: rem(40px);
					margin: 0px 1px;
					min-width: 0;
					border-radius: 50%;
					&:hover {
						background-color: #d8d8d8;
						min-width: 0;
						border-radius: 50%;
						line-height: rem(40px);
						width: rem(40px);
						height: rem(40px);
					}
					&.active {
						
						background-color: #d8d8d8;
						min-width: 0;
						border-radius: 50%;
						line-height: rem(40px);
						width: rem(40px);
						height: rem(40px);
						margin: 0px 1px;
						md-icon {
							color: #d65439;
						}
					}
					md-icon {
						color: #828282;
					}
				}

				&.icons {
					text-align: right;
					white-space: nowrap;
					padding-right: 25px;
				}
			}
			&.active {
				color: $color-gray-base;
				background-color: #f5f5f5;
			}	
			&:hover {
				color: $color-gray-base;
				background-color: #f5f5f5;
			}
		}
	}

	.list-table-scrollable {
		overflow-y: scroll;
		border-bottom-left-radius: 4px;
		border-bottom-right-radius: 4px;
		border-bottom: 1px solid #e1e1e1;

		& table {
			border-top-left-radius: 0;
			border-top-right-radius: 0;
		}

		& td {
			min-width: 80px;
		}
	}
}

@media screen and (min-width: 1280px) {
	#list-table-container {
		margin-left: rem(120px);
		margin-right: rem(120px);
	}
}



